import React, { useState, useContext, useReducer } from "react";

import { CardContext } from "../Card";
import chevronDownImage from "./Assets/SVG/chevron-down-solid.svg";
import "../../Animations/rotateButton.css";

// const reducer = key => key +1;
function ExpandButton() {
  //   const { setIsExpanded } = useContext(CardContext);
  //   const [id, updateId] = useReducer(reducer, 0);
  //   function update() {
  //       setIsExpanded({
  //           rotated
  //       });
  //       updateId();
  //   };

  const [bgLinkButton, setBgLinkButton] = useState("white");
  const [rotate, setRotate] = useState(false);
  const [rotated, setRotated] = useState(false);
  var rotateClass;
  const buttonStyle = {
    borderRadius: 40,
    border: "none",
    padding: 14,
    backgroundColor: bgLinkButton,
    outline: "none",
    marginLeft: "auto",
    imageStyle: {
      display: "block",
      height: 24,
      width: 24,
      maxHight: 50,
      maxWidth: 50,
      color: "rgba(0, 0, 0, 0.54)",
    },
  };
  if (rotate) {
    rotateClass = "rotate180";
    console.log("Facing UP");
  } else {
    rotateClass = "rotate0";
    console.log("Facing DOWN");
  }
  return (
    <CardContext.Consumer>
      {({ isExpanded, setIsExpanded }) => (
        <button
          id="linkButton"
          className="rotate"
          onMouseEnter={() => setBgLinkButton("#fafafa")}
          onMouseLeave={() => setBgLinkButton("white")}
          onClick={() => {
            setRotate(!rotate);
            setRotated(!rotated);
            setIsExpanded(!rotated);
          }}
          onAnimationEnd={() => setRotated(!rotated)}
          rotate={rotateClass}
          style={buttonStyle}
        >
          <img style={buttonStyle.imageStyle} src={chevronDownImage} alt="" />
        </button>
      )}
    </CardContext.Consumer>
  );
}

export default ExpandButton;
