import React, { useState, createContext, useReducer } from "react";
import CardHeader from "./CardHeader";
import CardMedia from "./CardMedia";
import CardContent from "./CardContent";
import CardLinks from "./CardLinks";

export const CardContext = createContext({
    isExpanded: false,
    setIsExpanded: () => {}
});

function Card() {

  const [isExpanded, setIsExpanded] = useState(false);
  var mainContent;
  const descriptionContent =
    "The BMW M3 is a high-performance version of the BMW 3 Series, developed by BMW's in-house motorsport division, BMW M GmbH.";

  if(!isExpanded){
    mainContent =
    'The E36 M3 is powered by the BMW S50 straight-six engine. It was the first M3 to use a six-cylinder engine, which has since been used the majority of M3 models (albeit in turbocharged form since 2014). In most countries, the initial 2,990 cc (182 cu in) version generated 210 kW (286 PS; 282 hp) at 7,000 rpm and 320 N⋅m (236 lb⋅ft) at 3,600 rpm. North American models (except for the limited edition Canadian "M3 Euro-Spec" model) used the less powerful BMW S50B30US engine instead. The facelift models in late 1995 were upgraded to a 3,201 cc (195 cu in) version of the BMW S50 engine, generating 236 kW (316 hp) at 7,400 rpm and 350 N⋅m (258 lbf⋅ft) at 3,250 rpm. North American models used the less powerful BMW S52 engine instead.';
  }
  else{
      mainContent = '';
  }
  const card = {
    maxWidth: 345,
    margin: "50px auto",
    justifyContent: "center",
    borderRadius: 4,
    position: "relative",
    boxSizing: "inherit",
    boxShadow: "0px 2px 1px 1px rgba(0, 0, 0, 0.2)",
  };
  console.log("expanded from context: "+isExpanded);
  return (
    <CardContext.Provider value={{isExpanded, setIsExpanded}}>
      <div style={card}>
        <CardHeader />
        <CardMedia />
        <CardContent content={descriptionContent} />
        <CardLinks />
        <CardContent content={mainContent} textColor="black" />
      </div>
    </CardContext.Provider>
  );
}
export default Card;
