import React from "react";

function CardContent({content, textColor}) {

    const cardContentStyle = {
        backgroundColor: 'white',
        padding: 16,
        paragraphStyle:{
            margin: 0,
            color: textColor || 'rgba(0, 0, 0, 0.54)',
            fontSize: '0.875rem',
            fontWeight: '400',
            lineHeight: '1.43',
            letterSpacing: '0.01071em'
        }
    }
  return (
    <div
        style={cardContentStyle}
    >
      <p
        style={cardContentStyle.paragraphStyle}
      >
        {content}
      </p>
    </div>
  );
}

export default CardContent;