import React, { useState } from "react";

function MenuButton() {
  const [bgColor, setBgColor] = useState("white");
  const buttonStyle = {
    decoration: "none",
    border: "none",
    padding: 0,
    borderRadius: 40,
    height: 40,
    width: 40,
    backgroundColor: bgColor,
    outline: 'none',
    menuButtonStyles: {
      buttonWrapper: {
        margin: 0,
        maxHeight: 40,
        maxWidth: 40,
        width: 40,
        borderRadius: 40,
        menuItem: {
          margin: "2px auto",
          height: 4,
          width: 4,
          backgroundColor: "rgba(0, 0, 0, 0.54)",
          borderRadius: 10,
          display: "flex",
        },
      },
    }
  };

  return (
    <button
      className='pulse'
      onMouseEnter={() => setBgColor("#fafafa")}
      onMouseLeave={() => setBgColor("white")}
      style={buttonStyle}
    >
      <div style={buttonStyle.menuButtonStyles.buttonWrapper}>
        <span
          style={buttonStyle.menuButtonStyles.buttonWrapper.menuItem}
        />
        <span
          style={buttonStyle.menuButtonStyles.buttonWrapper.menuItem}
        />
        <span
          style={buttonStyle.menuButtonStyles.buttonWrapper.menuItem}
        />
      </div>
    </button>
  );
}

export default MenuButton;
