import React, { useState } from "react";
import shareImage from "./Assets/SVG/share-alt-solid.svg";
import "../../Animations/pulseButtonBg.css";


function ShareButton() {
  const [bgLinkButton, setBgLinkButton] = useState("white");
  const [pulse, setPulse] = useState(0);
  const buttonStyle = {
    borderRadius: 40,
    border: "none",
    padding: 14,
    backgroundColor: bgLinkButton,
    outline: "none",
    imageStyle: {
      height: 24,
      width: 24,
      maxHight: 50,
      maxWidth: 50,
      color: "rgba(0, 0, 0, 0.54)",
    },
    expandButtonStyle: {
      marginLeft: "auto",
    },
  };
  return (
    <button
      id="linkButton"
      className='pulse'
      onMouseEnter={() => setBgLinkButton("#fafafa")}
      onMouseLeave={() => setBgLinkButton("white")}
      onClick={() => {
        setPulse(1);
      }}
      onAnimationEnd={() => setPulse(0)}
      pulse={pulse}
      style={buttonStyle}
    >
      <img style={buttonStyle.imageStyle} src={shareImage} alt="" />
    </button>
  );
}

export default ShareButton;
