import React, {useState} from 'react';

function MenuLink({to, title}) {
    const [fontColor, setFontColor] = useState('lightgrey');
    const styles = {
        padding: '0px 30px',
        boxSizing: 'border-box',
        textDecoration: 'none',
        margin: '0 5px',
        fontSize: 14,
        lineHeight: '3.14286',
        fontWeight: 400,
        letterSpacing: '-.01em',
        color: fontColor
    }
    console.log("LINK  "+fontColor);

    return(
            <a
                id="link"
                style={styles}
                rel="noopener noreferrer"
                href={to}
                onMouseEnter={() => setFontColor('white')}
                onMouseLeave={() => setFontColor('lightgrey')}
            >
                {title}
            </a>
    );
};

export default MenuLink;
