import React, { useState } from "react";
import regHeart from "./Assets/SVG/heart-regular.svg";
import solidHeart from "./Assets/SVG/heart-solid.svg";
import "../../Animations/pulseButtonBg.css";

function LikeButton() {
  var image = "";
  const [bgLinkButton, setBgLinkButton] = useState("white");
  const [likeState, setLikeState] = useState(false);
  const [pulse, setPulse] = useState(0);
  const buttonStyle = {
    borderRadius: 40,
    border: "none",
    padding: 14,
    backgroundColor: bgLinkButton,
    outline: "none",
    imageStyle: {
      height: 24,
      width: 24,
      maxHight: 50,
      maxWidth: 50,
    },
  };
  if (likeState) {
    image = solidHeart;
  } else {
    image = regHeart;
  }
  return (
    <button
      id="linkButton"
      className='pulse'
      onMouseEnter={() => setBgLinkButton("#fafafa")}
      onMouseLeave={() => setBgLinkButton("white")}
      onClick={() => {
        setLikeState(!likeState);
        setPulse(1);
      }}
      onAnimationEnd={() => setPulse(0)}
      pulse={pulse}
      style={buttonStyle}
    >
      <img style={buttonStyle.imageStyle} src={image} alt="" />
    </button>
  );
}
export default LikeButton;
