import React from 'react';
import User from './User'
import MenuButton from './MenuButton'

var moment = require("moment");

function CardHeader(){
    const cardHeaderStyles = {
        backgroundColor: 'white',
        boxSizing: 'inherit',
        padding: 16,
        display: 'flex',
        borderTopLeftRadius: 4,
        borderTopRightRadius: 4,
        tileHeader: {
            margin: 0,
            fontSize: '0.875rem',
            lineHeight: 1.43,
            width: 217
        },
        titleParagraph: {
            margin: 0,
            fontSize: '0.875rem',
            lineHeight: 1.43,
            color: 'rgba(0, 0, 0, 0.54)'
        }
    }
    var date = moment().format('LL');

    return(
        <div style={cardHeaderStyles}>
            <User/>
            <div>
                <h1 style={cardHeaderStyles.tileHeader}>E36 M3 Evolution</h1>
                <p style={cardHeaderStyles.titleParagraph}>{date}</p>
            </div>
            <MenuButton/>
        </div>
    );
}

export default CardHeader;